<template>
<h2>숫자 배열 조작과 구구단 출력</h2>
<div id="app">
    <ul>
        <li v-for="(item, index) in myArray" v-bind:key="item">{{ item }}
            <button v-on:click="modifyNumber(index)">변경</button>
            <button v-on:click="deleteNumber(index)">삭제</button>
        </li>
        <br/>
        <button v-on:click="addNumber()">추가</button>
        <button v-on:click="sortData()">소트</button>
        <button v-on:click="showGuGu()">구구단 보이기</button>

        <ul v-if="show">
            <li v-for="item in myArray" v-bind:key="item">
                <p v-for="item2 in 9" v-bind:key="item2">{{item}}x{{item2}}={{item * item2}}</p>
            </li>
        </ul>
    </ul>
</div>
</template>
<script>
export default {
    el: '#app',
    data() {
        return {
            myArray: [2,3,4,5,6,7,8,9],
            show:false
        }
    },
    methods: {
        sortData: function () {
                this.myArray = this.myArray.filter (function (item, idx, array) {
                    return array.indexOf(item) === idx;
                });

                this.myArray.sort(function(a,b) {
                    return (a < b ? -1 : 1);
                });
            },
            addNumber: function() {
              this.myArray.push(1);
            },
            modifyNumber: function(index) {
                this.myArray.splice(index, 1, this.myArray[index] + 1);
            },
            deleteNumber: function(index) {
                this.myArray.splice(index, 1);
            },
            showGuGu: function() {
                this.show = true;
            }
    }
}
</script>
<style>
@charset "UTF-8";
html {
	width: 80%;
	margin-right: auto;
	margin-left: auto;
	font-family: sans-serif;
	font-size: 16px;
	line-height: 1.5;
}

input,button,select,optgroup,textarea {
	font-family: inherit;
	font-size: inherit;
	line-height: inherit;
}

button {
	cursor: pointer;
	padding: 6px 12px;
	border-radius: 6px;
	color: #fff;
	border: 2px;
	background-color: #007bff;
	transition: background-color .2s
}
button:hover {
	background-color: #0069d9;
}
button:active {
	background-color: #003c7c;
}
button:disabled {
	opacity: .5;
	pointer-events: none;
}

input {
	padding: 6px 12px;
	border-radius: 6px;
	color: #495057;
	border: 2px solid #ced4da;
}

textarea {
	width: 500px;
	height: 200px;
}
select {
	border: 2px solid #ced4da;
}

</style>